.nav{
    width:100%;
    position:fixed;
    text-align:center;
    padding:1% 0%;
    top:0;
    z-index:1;
    transition-timing-function:ease-in;
    transition:all 0.5s;
}
.nav_black{
    background-color:black;
    height:70px;
}
.nav_logo{
    width:130px;
    object-fit:contain;
}
